import React from 'react';
import ReactDOM from 'react-dom';
import logo from './Me.jpg';
import fb from './icons/fb.svg';
import tw from './icons/tw.svg';
import lin from './icons/in.svg';
import email from './icons/email.svg';
import styled, {keyframes} from 'styled-components';
import './BodyPage.css';

const Name = styled.h2`
    color: white;
    text-align: center;
    font-size: 2.5em;
`;

const Prof = styled.h2`
    color: white;
    text-align: center;
    font-size: 1.4em;
`;

const HrLine = styled.div`
    height: 3px;
    background-color: white;
    margin: 0 48%;
`;

const ListContact = styled.div`
    position: relative;
    display: block;
    text-align: center;
    a {
        text-decoration: none;
    }
    a img {
        height: 40px;
        width: 40px;
        margin: 10px 10px;
    }
    a img:hover {
        background-color: #808080;
        webkit-transition: -webkit-transform 1.1s;
        -moz-transition: -moz-transform 1.1s;
        -ms-transition: -moz-transform 1.1s;
        transition: transform 1.1s;
        -o-transform: rotate(360deg);
        -ms-transform: rotate(360deg);
        -moz-transform: rotate(360deg);
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
`;

export default class BodyPage extends React.Component{
    render(){
        return (
            <div>
                <img id="logo" src={logo} />
                <Name>Дрожжин Дмитрий</Name>
                <Prof>Developer React JS(junior)</Prof>
                <HrLine />
                <ListContact>
                    <a href="mailto:drozhzhindmitriy@icloud.com"><img src={email} /></a>
                    <a href="http://linkedin.com/https://www.linkedin.com/in/%D0%B4%D0%BC%D0%B8%D1%82%D1%80%D0%B8%D0%B9-%D0%B4%D1%80%D0%BE%D0%B6%D0%B6%D0%B8%D0%BD-60483a164"><img src={lin} /></a>
                    <a href="#twitter"><img src={tw} /></a>
                    <a href="https://www.facebook.com/profile.php?id=1129151078"><img src={fb} /></a>
                </ListContact>
            </div>
        )
    }
}


